import { useEffect, useState } from "react"
const assets = require.context('../../../public/assets', true)

const Image = (props) => {
    useEffect(() => {
        console.log(process.env.PUBLIC_URL + `${props.filePath}`)
    })
    
    return (
        <img
            src = {assets(props.filePath).default}
            className = {props.className}
            alt = {props.alt}
            title = {props.alt}
        />
    )
}

export default Image